.app {
  place-self: center;
  position: relative;
  width: 16.25rem;
  border: 0.625rem solid var(--white);
  border-radius: 1.75rem;
  box-shadow: 0.625rem 1.25rem 1.875rem #e6e0eb;
  animation: app-vibrate;
  animation-duration: 1.5s;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  animation-delay: 1200ms;
}
.app::before {
  content: "";
  width: 8.125rem;
  height: 1.4375rem;
  border-radius: 0 0 0.9375rem 0.9375rem;
  background-color: var(--white);
  position: absolute;
  left: 50%;
  top: -1%;
  transform: translateX(-50%);
}

.header {
  display: flex;
  align-items: center;
  gap: 0.625rem;
  border-radius: 1.125rem 1.125rem 0.3125rem 0.3125rem;
  padding: 1.875rem 0.9375rem 0.625rem;
  color: var(--p-violet);
  background-image: linear-gradient(to right, var(--lt-violet), var(--lt-magenta));
}
.header__arrow {
  color: var(--p-violet);
}
.header__arrow:hover {
  color: var(--white);
}
.header__avatar {
  display: inline-block;
  position: relative;
  font-size: 0;
  color: var(--p-violet);
}
.header__avatar:hover {
  color: var(--white);
}
.header__avatar img {
  border: 0.125rem solid;
  border-radius: 50%;
}
.header__avatar::before {
  content: "";
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
  background-color: var(--p-violet);
  position: absolute;
  top: 100%;
  left: 100%;
  transform: translate(-100%, -100%);
  animation: online-ind;
  animation-duration: 1s;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
  animation-delay: 3.1s;
}
.header__username {
  flex: 1;
  font-size: 0.625rem;
  font-weight: 700;
}
.header__username a {
  color: var(--p-violet);
  transition: all 300ms ease-in-out;
}
.header__username a:hover {
  color: var(--white);
}
.header__username span {
  font-size: 0.5rem;
  color: var(--pale-violet);
}
.header__dots {
  width: 0.625rem;
  color: var(--p-violet);
}
.header__dots:hover {
  color: var(--white);
}

.message {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding: 0.75rem 0.5rem;
  font-size: 0.5rem;
  font-weight: 500;
  background-color: var(--lt-gr-violet);
}
.message > :not(:last-child) {
  margin-bottom: 0.5rem;
}
.message > :nth-child(7) {
  margin-bottom: 0;
}
.message > :nth-child(2), .message > :nth-child(5) {
  margin-bottom: 0.9375rem;
}
.message form {
  width: 0;
  height: 0;
  margin: 0;
}
.message__left {
  color: var(--mod-violet);
  background-color: #e6e0eb;
  padding: 0.5rem;
  width: 62%;
  border-radius: 0.625rem 0.625rem 0.625rem 0.1875rem;
}
.message__left-txt1 {
  transform: scale(0);
  animation: sms-l1;
  animation-duration: 700ms;
  animation-timing-function: ease-in;
  animation-delay: 3.8s;
  animation-fill-mode: forwards;
}
.message__left-txt2 {
  transform: scale(0);
  animation: sms-l2;
  animation-duration: 700ms;
  animation-timing-function: ease-in;
  animation-delay: 5.1s;
  animation-fill-mode: forwards;
}
.message__left-txt3 {
  transform: scale(0);
  animation: sms-l3;
  animation-duration: 700ms;
  animation-timing-function: ease-in;
  animation-delay: 10.9s;
  animation-fill-mode: forwards;
}
.message__right {
  align-self: flex-end;
}
.message__right-img {
  padding: 0;
  transform: scale(0);
  animation: sms-r1;
  animation-duration: 700ms;
  animation-timing-function: ease-in;
  animation-delay: 6.7s;
  animation-fill-mode: forwards;
}
.message__right-img :not(:last-child) {
  margin-right: 0.4375rem;
}
.message__right-img img {
  border-radius: 0.625rem;
  width: 2.5rem;
  height: 2.5rem;
}
.message__right-txt1 {
  transform: scale(0);
  animation: sms-r2;
  animation-duration: 700ms;
  animation-timing-function: ease-in;
  animation-delay: 8s;
  animation-fill-mode: forwards;
}
.message__right-txt2 {
  transform: scale(0);
  animation: sms-r3;
  animation-duration: 700ms;
  animation-timing-function: ease-in;
  animation-delay: 9.3s;
  animation-fill-mode: forwards;
}
.message__right-txt1, .message__right-txt2 {
  max-width: 60%;
  padding: 0.5rem;
  color: var(--dk-violet);
  background-color: var(--white);
  border-radius: 0.625rem 0.625rem 0 0.625rem;
  box-shadow: 0.125rem 0.3125rem 0.3125rem #e6e0eb;
}
.message__book {
  display: flex;
  align-items: center;
  color: var(--white);
  width: 75%;
  padding: 0.5rem 1rem 0.5rem 0.1875rem;
  background-image: linear-gradient(to right, var(--lt-magenta), var(--lt-violet));
  border-radius: 0.8125rem 0.8125rem 0.8125rem 0.1875rem;
  transform: scale(0);
  animation: book;
  animation-duration: 700ms;
  animation-timing-function: ease-in;
  animation-delay: 12.6s;
  animation-fill-mode: forwards;
}
.message__book--check {
  width: 0.8125rem;
  height: 0.8125rem;
  border: 0.125rem solid var(--vry-lt-magenta);
  border-radius: 50%;
  cursor: pointer;
  position: absolute;
  left: 6%;
  transition: border-color 300ms ease-in-out;
}
.message__book--radio {
  margin: 0;
  visibility: hidden;
  width: 0.0625rem;
  height: 0.0625rem;
}
.message__book input[type=radio]:hover ~ label {
  color: var(--white);
}
input[type=radio]:hover ~ .message__book--check {
  border-color: var(--white);
}
.message__book input[type=radio]:checked ~ label {
  color: cyan;
}
input[type=radio]:checked ~ .message__book--check {
  border-color: cyan;
}
input[type=radio]:checked ~ .message__book--check::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
  border-radius: 50%;
  width: 0.3125rem;
  height: 0.3125rem;
  background-color: cyan;
}
.message__book label {
  display: block;
  position: relative;
  padding: 0.09375rem 0.3125rem 0.09375rem 1.25rem;
  cursor: pointer;
  color: var(--pale-magenta);
  transition: color 300ms ease-in-out;
  z-index: 1;
}
.message__book--price {
  flex: 1;
  text-align: right;
  font-size: 0.875rem;
  font-weight: 700;
}

.input-section {
  padding: 0 0.5rem 0.75rem;
  border-radius: 0 0 1.125rem 1.125rem;
  background-color: var(--lt-gr-violet);
}

.input {
  display: flex;
  align-items: center;
  background-color: var(--white);
  border-radius: 1.25rem;
  padding: 0.25rem 0.5625rem 0.25rem 1.25rem;
}
.input__message {
  flex: 1;
  border: none;
  outline: none;
  border-radius: 1.25rem;
  font-size: 0.625rem;
}
.input ::placeholder {
  font-size: 0.625rem;
  color: var(--grayish-blue);
  opacity: 1;
  /* Firefox */
}
.input :-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  font-size: 0.625rem;
  color: var(--grayish-blue);
}
.input ::-ms-input-placeholder {
  /* Microsoft Edge */
  font-size: 0.625rem;
  color: var(--grayish-blue);
}
.input__aright {
  position: absolute;
  right: 1.625rem;
  color: var(--p-violet);
  font-size: 0.75rem;
}
.input__send {
  width: 1.5625rem;
  height: 1.5625rem;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  background-color: var(--vry-dk-violet);
  color: var(--white);
  transition: background-color 300ms ease-in-out;
}
.input__send:hover {
  background-color: var(--lt-violet);
}